<template>
<AppHeader />
<h1>Hello {{ name }}, Welcome on Add RestaurantPage</h1>
<form class="add">
    <input type="text" placeholder="Enter Name" name="name" v-model="restaurant.name">
    <input type="text" placeholder="Enter Address" name="address" v-model="restaurant.address">
    <input type="text" placeholder="Enter Contact" name="contact" v-model="restaurant.contact">
    <button type="button" v-on:click="addRestaurant">Add New Restaurant</button>
</form>
</template>

    
<script>
import AppHeader from "./App-Header.vue"
import axios from "axios";
export default {
    name: 'Add-Item',
    data() {
        return {
            name: '',
            restaurant: {
                name:'',
                address:'',
                contact:''
            }
        }
    },
    components: {
        AppHeader
    },
    mounted() {
        let user = localStorage.getItem('user-info');
        if (!user) {
            this.$router.push({
                name: "SignUp"
            });
        }
        const userObj = JSON.parse(user);
        this.name = userObj.name;
    },
    methods: {
        async addRestaurant() {
            if(this.restaurant.name == '' || this.restaurant.address == '' || this.restaurant.contact == '') {
                alert('Fill up the form');
            } else {
                const result = await axios.post('http://localhost:3000/restaurant',this.restaurant)
                if(result.status == 201) {
                    this.$router.push({ name:'Home' })
                }

            }
        }
    }

};
</script>

    
