<template>
<Header />
<h1>Hello {{ name }}, Welcome on Update Restaurant Page</h1>
<form class="update">
    <input type="text" placeholder="Enter Name" name="name" v-model="restaurant.name">
    <input type="text" placeholder="Enter Address" name="address" v-model="restaurant.address">
    <input type="text" placeholder="Enter Contact" name="contact" v-model="restaurant.contact">
    <button type="button" v-on:click="UpdateRestaurant">Update Restaurant</button>
</form>
</template>

    
<script>
import Header from "./App-Header.vue"
import axios from "axios";
export default {
    name: 'Update-Item',
    data() {
        return {
            name: '',
            restaurant: {
                name:'',
                address:'',
                contact:''
            }
        }
    },
    components: {
        Header
    },
    methods: {
        async UpdateRestaurant() {
            //http://localhost:3000/restaurant
            if(this.restaurant.name == '' || this.restaurant.address == '' || this.restaurant.contact == '') {
                alert('Fill up the form');
            } else {
                const result = await axios.put(`http://localhost:3000/restaurant/${this.$route.params.id}`,{
                    name: this.restaurant.name,
                    address: this.restaurant.address,
                    contact: this.restaurant.contact
                })
                if(result.status == 200) {
                    this.$router.push({ name:'Home' })
                }

            }
        }
    },
    async mounted() {
        let user = localStorage.getItem('user-info');
        if (!user) {
            this.$router.push({
                name: "SignUp"
            })
        }
        let userObj = JSON.parse(localStorage.getItem('user-info'));
        this.name = userObj.name;

        const result = await axios.get(`http://localhost:3000/restaurant/${this.$route.params.id}`);
        this.restaurant = result.data
    }

};
</script>

    
