import Home  from './components/Home-Page.vue';
import SignUp from './components/User-SignUp.vue';
import Login from './components/UserLogin.vue';
import Add from './components/Add-Item.vue';
import Update from './components/Update-Item.vue';

import { createRouter, createWebHistory } from 'vue-router';

const routes = [
    {
        name:'Home',
        component:Home,
        path:'/'
    },
    {
        name:'SignUp',
        component:SignUp,
        path:'/sign-up'
    },
    {
        name:'Login',
        component:Login,
        path:'/login'
    },
    {
        name:'Add',
        component:Add,
        path:'/add'
    },
    {
        name:'Update',
        component:Update,
        path: '/update:id'
    }
]

const router = createRouter({
    history:createWebHistory(),
    routes
})

export default router